Lang.Helper.robotis_carCont_sensor_value = "왼쪽 접속 센서 : 접촉(1), 비접촉(0) 값 입니다.<br/>오른쪽 접촉 센서 : 접촉(1), 비접촉(0) 값 입니다.<br/>선택 버튼 상태 : 접촉(1), 비접촉(0) 값 입니다.<br/>최종 소리 감지 횟수 : 마지막 실시간 소리 감지 횟수 값 입니다.<br/>실시간 소리 감지 횟수 : 약 1초 안에 다음 소리가 감지되면 1씩 증가합니다.<br/>왼쪽 적외선 센서 : 물체와 가까울 수록 큰 값 입니다.<br/>오른쪽 적외선 센서 : 물체와 가까울 수록 큰 값 값 입니다.<br/>왼쪽 적외선 센서 캘리브레이션 값 : 적외선 센서의 캘리브레이션 값 입니다.<br/>오른쪽 적외선 센서 캘리브레이션 값 : 적외선 센서의 캘리브레이션 값 입니다.<br/>(*캘리브레이션 값 - 적외선센서 조정 값)";
Lang.Helper.robotis_carCont_cm_led = "4개의 LED 중 1번 또는 4번 LED 를 켜거나 끕니다.<br/>LED 2번과 3번은 동작 지원하지 않습니다.";
Lang.Helper.robotis_carCont_cm_sound_detected_clear = "최종 소리 감지횟 수를 0 으로 초기화 합니다.";
Lang.Helper.robotis_carCont_aux_motor_speed = "감속모터 속도를 0 ~ 1023 의 값(으)로 정합니다.";
Lang.Helper.robotis_carCont_cm_calibration = "적외선센서 조정 값(http://support.robotis.com/ko/: 자동차로봇> 2. B. 적외선 값 조정)을 직접 정합니다.";
Lang.Helper.robotis_openCM70_sensor_value = "최종 소리 감지 횟수 : 마지막 실시간 소리 감지 횟수 값 입니다.<br/>실시간 소리 감지 횟수 : 약 1초 안에 다음 소리가 감지되면 1씩 증가합니다.<br/>사용자 버튼 상태 : 접촉(1), 비접촉(0) 값 입니다.최종 소리 감지 횟수 : 마지막 실시간 소리 감지 횟수 값 입니다.<br/>실시간 소리 감지 횟수 : 약 1초 안에 다음 소리가 감지되면 1씩 증가합니다.<br/>사용자 버튼 상태 : 접촉(1), 비접촉(0) 값 입니다.";
Lang.Helper.robotis_openCM70_aux_sensor_value = "서보모터 위치 : 0 ~ 1023, 중간 위치의 값은 512 입니다.<br/>적외선센서 :  물체와 가까울 수록 큰 값 입니다.<br/>접촉센서 : 접촉(1), 비접촉(0) 값 입니다.<br/>조도센서(CDS) : 0 ~ 1023, 밝을 수록 큰 값 입니다.<br/>온습도센서(습도) : 0 ~ 100, 습할 수록 큰 값 입니다.<br/>온습도센서(온도) : -20 ~ 100, 온도가 높을 수록 큰 값 입니다.<br/>온도센서 :  -20 ~ 100, 온도가 높을 수록 큰 값 입니다.<br/>초음파센서 : -<br/>자석센서 : 접촉(1), 비접촉(0) 값 입니다.<br/>동작감지센서 : 동작 감지(1), 동작 미감지(0) 값 입니다.<br/>컬러센서 : 알수없음(0), 흰색(1), 검은색(2), 빨간색(3), 녹색(4), 파란색(5), 노란색(6) 값 입니다.<br/>사용자 장치 : 사용자 센서 제작에 대한 설명은 ROBOTIS e-매뉴얼(http://support.robotis.com/ko/)을 참고하세요.";
Lang.Helper.robotis_openCM70_cm_buzzer_index = "음계를 0.1 ~ 5 초 동안 연주 합니다.";
Lang.Helper.robotis_openCM70_cm_buzzer_melody = "멜로디를 연주 합니다.<br/>멜로디를 연속으로 재생하는 경우, 다음 소리가 재생되지 않으면 '흐름 > X 초 기다리기' 블록을 사용하여 기다린 후 실행합니다.";
Lang.Helper.robotis_openCM70_cm_sound_detected_clear = "최종 소리 감지횟 수를 0 으로 초기화 합니다.";
Lang.Helper.robotis_openCM70_cm_led = "제어기의 빨간색, 녹색, 파란색 LED 를 켜거나 끕니다.";
Lang.Helper.robotis_openCM70_cm_motion = "제어기에 다운로드 되어있는 모션을 실행합니다.";
Lang.Helper.robotis_openCM70_aux_motor_speed = "감속모터 속도를 0 ~ 1023 의 값(으)로 정합니다.";
Lang.Helper.robotis_openCM70_aux_servo_mode = "서보모터를 회전모드 또는 관절모드로 정합니다.<br/>한번 설정된 모드는 계속 적용됩니다.<br/>회전모드는 서보모터 속도를 지정하여 서보모터를 회전 시킵니다.<br/>관절모드는 지정한 서보모터 속도로 서보모터 위치를 이동 시킵니다.";
Lang.Helper.robotis_openCM70_aux_servo_speed = "서보모터 속도를 0 ~ 1023 의 값(으)로 정합니다.";
Lang.Helper.robotis_openCM70_aux_servo_position = "서보모터 위치를 0 ~ 1023 의 값(으)로 정합니다.<br/>서보모터 속도와 같이 사용해야 합니다.";
Lang.Helper.robotis_openCM70_aux_led_module = "LED 모듈의 LED 를 켜거나 끕니다.";
Lang.Helper.robotis_openCM70_aux_custom = "사용자 센서 제작에 대한 설명은 ROBOTIS e-매뉴얼(http://support.robotis.com/ko/)을 참고하세요.";
Lang.Helper.robotis_openCM70_cm_custom_value = "컨트롤 테이블 주소를 직접 입력하여 값을 확인 합니다.<br/>컨트롤 테이블 대한 설명은 ROBOTIS e-매뉴얼(http://support.robotis.com/ko/)을 참고하세요.";
